import React from "react";
import { Footer } from "../../sections";
import { TranslateBar } from "../../components";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";
import "./PrivacyPolicy.css"
import { NavLink } from "react-router-dom";
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";
import { back_button} from "../../resources/images";

const heading_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .5 }},
  hidden: { opacity: 0, x: -100, transition: { duration: .5 }},
}

const PrivacyPolicy = () => {
  const { loading, error, data } = useQuery(GET_CONTENT);

  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;

  const control = useAnimation()
  const [ref, inView] = useInView()


  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);

  return (
    <>
      <div className="p-10 bg-gradientBG">
        <motion.div 
        variants={heading_variants}
        ref = {ref}
        initial = "hidden"
        animate = {control}
        className="flex justify-between">
          <div className="flex">
            <NavLink to="/" className={`font-bold font-serif hover:text-gray-300`}>
              <img width="50px" src={back_button}/>
            </NavLink>
            <h1 className="font-bold font-sans drop-shadow-2xl shadow-dark text-4xl ml-10 text-almost-white">Privacy Policy</h1>
          </div>
          <TranslateBar/>
        </motion.div>
      </div>
      <div className="text-steel-black mx-20 leading-tight thisPage" dangerouslySetInnerHTML={{ __html: data.privacyPolicy[0].content }}></div>
      <Footer />
    </>
  );
};

export default PrivacyPolicy;
