import React from "react";
import { useQuery, gql } from '@apollo/client';

export const GET_CONTENT = gql`
  query {
    navBar {
      id
      logo_text
      navLinks {
        id
      }
    }
    header {
      id
      heading
      description_1
      button_text
      sub_heading
      description_2
    }
    aboutSection {
      id
      aboutHeading
      aboutBody
    }
    servicesSection {
      id
      title
      btnText1
      btnText2
      btnText3
      footerContent
    }
    contactSection {
      id
      smallText
    }
    teamMembers {
      id
      person_name
      person_position
      person_about
      linkedin
      twitter
      facebook
      person_image{
        id
        filename_download
        filename_disk
      }
      projects{
        id
        camsolProjects_id{
          id
          project_name
          project_detail
          project_photo{
            id
            filename_download
            filename_disk
          }
        }
      }
      skills{
        id
        name
        proficiency
      }
    }
    camsolProjects{
      id
      project_name
      project_detail
      project_photo{
        id
        filename_download
        filename_disk
      }
    }
    privacyPolicy{
      id
      content
    }
    termsAndConditions{
      id
      content
    }
  }
`;
