import React from "react";
import "./TranslateBar.css";
import {german_flag, english_flag, french_flag} from "../../resources/images";
import * as ReactDOM from 'react-dom/client';


function contains(selector, node, text) {
  var elements = node.querySelectorAll(selector);
  return [].filter.call(elements, function(element){
    return RegExp(text).test(element.textContent);
  });
}

function triggerHtmlEvent(element, eventName) {
  var event;
  if(document.createEvent) {
      event = document.createEvent('HTMLEvents');
      event.initEvent(eventName, true, true);
      element.dispatchEvent(event);
  } else {
      event = document.createEventObject();
      event.eventType = eventName;
      element.fireEvent('on' + event.eventType, event);
  } 
}

const TranslateBar = () => {

    const doc_old_method = () => {
      var $frame = document.getElementsByClassName('goog-te-menu-frame')[0]
      var $c = $frame.contentDocument || $frame.contentWindow.document;
      var language_button = contains(".goog-te-menu2-item span.text", $c, lang1)
      var language_button = contains(".goog-te-combo", $frame, lang1)
      if (language_button.length == 0){
          // language_button = contains(".goog-te-menu2-item span.text", $c, lang2)
          language_button = contains(".goog-te-combo", $frame, lang2)

      }
      console.log(language_button)

      language_button[0].change()
    }

    const translate_german = () => {
      var lang = "de"
      var select_div = document.getElementsByClassName('goog-te-combo')
      triggerHtmlEvent(select_div[0], "change")
      select_div[0].value = lang
      
      triggerHtmlEvent(select_div[0], "change")
      
    };

    const translate_french = () => {
      var lang = "fr"
      var select_div = document.getElementsByClassName('goog-te-combo')
      select_div[0].value = lang
      
      triggerHtmlEvent(select_div[0], "change")
    };

    const translate_english = () => {
      var lang = "en"
      var select_div = document.getElementsByClassName('goog-te-combo')
      select_div[0].value = lang
      
      triggerHtmlEvent(select_div[0], "change")
    };


    return (
        <div>
            <div onClick={translate_german}
                title="German"
                className="inline-flex mx-2 flex-col text-center w-[50px] hover:cursor-pointer">
                <img className="h-[50px] w-[50px] rounded-full" alt="german_flag"
                    src={german_flag}/>
            </div>
            <div onClick={translate_french}
                title="French"
                className="inline-flex mx-2 flex-col text-center w-[50px] hover:cursor-pointer">
                <img className="h-[50px] w-[50px] rounded-full" alt="french_flag"
                    src={french_flag}/>
            </div>
            <div onClick={translate_english}
                title="English"
                className="inline-flex mx-2 flex-col text-center w-[50px] hover:cursor-pointer">
                <img className="h-[50px] w-[50px] rounded-full" alt="english_flag"
                    src={english_flag}/>
            </div>
        </div>
    );
};

export default TranslateBar;
