import React, { Component } from 'react'
import Chart from 'react-google-charts'

const pieData = [
  ['Task', 'Hours per Day'],
  ['Work', 11],
  ['Eat', 2],
  ['Commute', 2],
  ['Watch TV', 2],
  ['Sleep', 7],
]
const pieOptions = {
  title: "Team Member's Skills",
  pieHole: 0.4,
}
const DonutChart = () => {

    return (
      <div className="h-[400px] w-[650px] mt-5 ">
        <Chart
          width={'100%'}
          height={'100%'}
          chartType="PieChart"
          loader={<div>Loading Chart</div>}
          data={pieData}
          options={pieOptions}
          rootProps={{ 'data-testid': '3' }}
        />
      </div>
    )
}
export default DonutChart