import React from "react";
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";
import { NavLink } from "react-router-dom";
import { TeamCard, TranslateBar } from "../../components";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";
import { back_button} from "../../resources/images";

const heading_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .5 }},
  hidden: { opacity: 0, x: -50, transition: { duration: .5 }},
}

function Teams() {
  const { loading, error, data } = useQuery(GET_CONTENT);

  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;

  const control = useAnimation()
  const [ref, inView] = useInView()


  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);


  return (
    <section id="services" className="relative h-auto pt-10 mb-20 items-center bg-cover m-0">
      <div className="bg-gradient-to-br w-full from-steel-blue to-voilet absolute top-0 left-0 h-[350px]">
        <motion.div
         variants={heading_variants}
         ref = {ref}
         initial = "hidden"
         animate = {control}
         className="flex flex-row ml-20 mt-20 py-auto flex justify-between pr-10 justify-content-center">
          <div className="flex">
            <NavLink to="/" className={`font-bold font-serif hover:text-gray-300`}>
              <img width="50px" src={back_button}/>
            </NavLink>
            <h1 className="font-bold font-sans drop-shadow-2xl shadow-dark text-4xl ml-10 text-almost-white">Our Team</h1>
          </div>
          <TranslateBar/>
        </motion.div>
      </div>
      <div className="relative top-[100px] left-0 flex flex-row flex-wrap px-5 w-full">
      { data.teamMembers.map((card) => {
          let social_links = [card.linkedin, card.twitter, card.facebook]
          let image_name = "https://3pfrf4ic.directus.app/assets/" + card.person_image['id'] + "?access_token=tnjmJ9Ga_lvXO03bYLUKpk5Qd30HuEZk"
          return (
            <div className="mt-20 w-[400px] mx-auto" key={card.id}>
              <TeamCard
                all_structure = {card}
                image_name = {image_name}
                person_name = {card.person_name}
                person_position = {card.person_position}
                person_about = {card.person_about}
                social_links = {social_links}
              />
            </div>
          )
        })
      }
      </div>

      <div className="h-[200px]"></div>
    </section>
  );
}

export default Teams;
