import React from "react";
import {Teams, Footer } from "../../sections";
import { Carousel, CarouselItem, TranslateBar, DonutChart } from "../../components";
import { useLocation, useNavigate } from "react-router-dom"
import { NavLink } from "react-router-dom";
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";
import { back_button} from "../../resources/images";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import * as All from '@fortawesome/free-solid-svg-icons'

const heading_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .5 }},
  hidden: { opacity: 0, x: -100, transition: { duration: .5 }},
}

const image_rotate = {
  visible: { rotate: 0, x: 0, transition: { duration: .7 }},
  hidden: { rotate: -180, x: -300, transition: { duration: .7 }},
}

const TeamMemberDetails = () => {
  const location = useLocation()
  const data = location['state']
  const navigate = useNavigate()
  const return_home = (id) => {
    navigate("/Teams", {
      state: all_structure
    })
  }

  if(!data){
    return_home
  }

  const img = data['person_image']['id']
  const person_image = "https://3pfrf4ic.directus.app/assets/" + img + "?access_token=tnjmJ9Ga_lvXO03bYLUKpk5Qd30HuEZk"
  const person_name = data['person_name']
  const person_first_name = person_name.split(" ")[0];
  const person_position = data['person_position']
  const person_about = data['person_about']
  const item_name = "dummy name"

  const person_projects = data['projects']

  const control = useAnimation()
  const [ref, inView] = useInView()


  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);

  return (
    <>
      <div className="p-10 bg-gradientBG">
        <motion.div 
        variants={heading_variants}
        ref = {ref}
        initial = "hidden"
        animate = {control}
        className="flex justify-between">
          <div className="flex">
            <NavLink to="/Teams" className={`font-bold font-serif hover:text-gray-300`}>
              <img width="50px" src={back_button}/>
            </NavLink>
            <h1 className="font-bold font-sans drop-shadow-2xl shadow-dark text-4xl ml-10 text-almost-white">{person_first_name}</h1>
          </div>
          <TranslateBar/>
        </motion.div>
      </div>

      <div className="mx-auto mt-10 p-10 rounded-3xl h-auto lg:h-[70vh] lg:w-[80%] bg-almost-white flex flex-col lg:flex-row shadow-2xl">
        <div className="text-center mx-auto w-[250px]">
          <motion.div 
          variants={image_rotate}
          ref = {ref}
          initial = "hidden"
          animate = {control}
          className="border-4 rounded-full border-bay-of-many w-[250px] h-[250px] drop-shadow-2xl">
            <img src={person_image} className="w-[100%] rounded-full"/>
          </motion.div>
          <h3 className="font-bold mt-3 text-2xl font-mono">{person_name}</h3>
          <h4 className="font-semibold text-sm font-mono">{person_position}</h4>
          <div className="text-gray-400 flex text-mdlg mt-2">
            <div className="w-[12%]"><FontAwesomeIcon icon={All.faMapMarkerAlt} /></div> <div className="w-[88%]"><p className="font-normal">Degree with location link</p></div>
          </div>
        </div>
        <div className="w-[100%] lg:w-[80%] text-center lg:text-right mt-10 lg:mt-0 flex flex-col justify-between">
          <div>
            <h4 className="text-4xl font-bold mx-auto lg:ml-auto font-display">about me</h4>
            <div className="text-mdlg w-[100%] lg:w-[75%] ml-auto" dangerouslySetInnerHTML={{ __html: person_about }}></div>
          </div>
          <div>
            <h4 className="text-2xl font-bold mt-5 lg:mt-0 mx-auto lg:ml-auto font-sans">latest projects</h4>
            <div className="w-[100%] lg:w-[75%]  h-[auto] lg:h-[200px] ml-auto mt-2">
              <Carousel show={2}>
                { person_projects.map((project) => {
                    let project_dets = project.camsolProjects_id
                    let project_name = project_dets['project_name']
                    let project_details = project_dets['project_detail']
                    let img = project_dets['project_photo']
                    let project_photo = "https://3pfrf4ic.directus.app/assets/" + img['id'] + "?access_token=tnjmJ9Ga_lvXO03bYLUKpk5Qd30HuEZk"
                    return (
                      <CarouselItem show={2} key={project.id} item_name={project_name}><img src={project_photo} alt="placeholder" style={{width: '100%', height: '100%', objectFit: 'cover'}} /></CarouselItem>
                    )
                  })
                }
                <CarouselItem show={2} item_name={item_name}><img src="https://via.placeholder.com/300" alt="placeholder" style={{width: '100%', height: '100%', objectFit: 'cover'}} /></CarouselItem>
                <CarouselItem show={2} item_name={item_name}><img src="https://via.placeholder.com/300" alt="placeholder" style={{width: '100%', height: '100%', objectFit: 'cover'}} /></CarouselItem>
                <CarouselItem show={2} item_name={item_name}><img src="https://via.placeholder.com/300" alt="placeholder" style={{width: '100%', height: '100%', objectFit: 'cover'}} /></CarouselItem>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
      <div className="mx-20 mt-20">
        <DonutChart/>
      </div>
    </>
  );
};

export default TeamMemberDetails;
