import React  from "react";
import { motion, useAnimation } from "framer-motion";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { selina, linkedin, twitter, facebook } from "../../resources/images";
import * as ReactDOM from 'react-dom/client';
import { ReadMore } from "../";
import { useNavigate } from "react-router-dom";


function removeHTML(str){
    var tmp = document.createElement("DIV");
    tmp.innerHTML = str;
    return tmp.textContent || tmp.innerText || "";
}

const card_variants = {
  visible: { opacity: 1, y: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, y: 200, transition: { duration: .6 }},
}

const TeamCard = ({all_structure, image_name, person_name, person_position, person_about, social_links}) => {
  const [isBooped, setIsBooped] = useState(false);
  const timing = 300
  // console.log(all_structure.id)
  const id = all_structure.id
  // console.log(id)

  const control = useAnimation()
  const [ref, inView] = useInView()

  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);

  const navigate = useNavigate()
  const open_user_details = (id) => {
    navigate("/team_member_details", {
      state: all_structure
    })
  }


  return (
    <motion.div
    variants={card_variants}
    ref = {ref}
    initial = "hidden"
    animate = {control}
    onClick = {open_user_details}
    className="shadow-2xl bg-almost-white mx-10 h-[auto] py-10 px-5 hover:cursor-pointer justify-items-center space-x-0 text-center rounded-xl flex flex-col justify-center">
      <img src={image_name} className="w-[40%] mx-auto rounded-full"/>
      <h3 className="font-bold mt-3 text-2xl font-mono">{person_name}</h3>
      <h4 className="font-semibold text-sm font-mono">{person_position}</h4>
      <div className="mt-10 text-mdsm">
        <ReadMore>{person_about}</ReadMore>
      </div>
      <div className="mt-10 flex flex-row flex-wrap justify-center">
        <a href={social_links[0]} target="blank" className="w-[50px] transition transform hover:cursor-pointer hover:scale-110"><img className="mx-auto rounded-full" src={linkedin}/></a>
        <a href={social_links[1]} target="blank" className="w-[50px] transition transform hover:cursor-pointer hover:scale-110"><img className="mx-auto" src={twitter}/></a>
        <a href={social_links[2]} target="blank" className="w-[50px] transition transform hover:cursor-pointer hover:scale-110"><img className="mx-auto rounded-full" src={facebook}/></a>
      </div>
    </motion.div>
  );
};

export default TeamCard;
