import React from 'react'
import './Carousel.css'
import { useEffect, useState } from "react";

const CarouselItem = (props) => {
  const {children, show, item_name} = props

  return(
    <div style={{padding: 8}} className='relative'>
      <div style={{width: `{calc(100% / ${show})}`, height: '100%'}}>
        {children}
      </div>
      <div className="w-full p-auto absolute bottom-2 text-center">
        <a href="" className="text-sm font-bold capitalize text-bay-of-many font-display">{item_name}</a>
      </div>
    </div>
  )
}

export default CarouselItem
